.busca-pet-container {
  width: 100%;
  max-width: 1400px;
  padding: 30px;
  margin: 32px auto;
}

.busca-pet-container header {
  display: flex;
  align-items: center;
}

.busca-pet-container header span {
  font-size: 10px;
  margin-left: 14px;
}

.busca-pet-container header img {
  height: 64px;
}

.busca-pet-container header a {
  width: 260px;
  margin-left: auto;
  margin-top: 0;
}

.busca-pet-container header button:hover {
  border-color: #999;
}

.busca-pet-container h1 {
  font-size: 25px;
  margin-top: 2px;
  margin-bottom: 24px;
}

.busca-pet-container ul {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 20px;
  list-style: none;
}

.busca-pet-container ul li {
  padding: 24px;
  border-radius: 8px;
  position: relative;
}

.busca-pet-container ul li button {
  position: absolute;
  right: 24px;
  top: 24px;
  border: 0;
}
.busca-pet-container ul li button:hover {
  opacity: 80%;
}

.busca-pet-topo-retangulo-container {
  width: 92.5%;
  max-width: 1180px;
  padding: 2px;
  margin: 25px auto;
  position: absolute;
  height: 110px;
  left: 25px;
  justify-content: center;
  top: 0px;
  background: rgba(0, 0, 0, 0.7);
  border-radius: 50px 50px 0px 0px;
}

.busca-pet-topo-retangulo-container h1 {
  color: aliceblue;
  text-align: center;
}
.busca-pet-topo-retangulo-container h2 {
  font-size: 20px;
  color: aliceblue;
  text-indent: 10px;
}

.busca-pet-baixo-retangulo-container {
  width: 92.5%;
  max-width: 1180px;
  padding: 2px;
  margin: 25px auto;
  position: absolute;
  height: 100px;
  left: 25px;
  justify-content: center;
  top: 291px;
  background: rgba(0, 0, 0, 0.7);
  border-radius: 0px 0px 50px 50px;
}

.profileUser-container {
  width: 100%;
  max-width: 1180px;
  padding: 0 30px;
  margin: 32px auto;
}

.profileUser-container header {
  display: flex;
  align-items: center;
}

.profileUser-container header span {
  font-size: 20px;
  margin-left: 24px;
}

.profileUser-container header img {
  height: 64px;
}

.profileUser-container header a {
  width: 260px;
  margin-left: auto;
  margin-top: 0;
}
.fotos {
  position: relative;
}
.btnlixo {
  width: 32px;
  height: 32px;
  margin-top: 10px;
  position: absolute;
}
.btnlixo :hover {
  width: 32px;
  height: 32px;
  border-radius: 5px 5px 5px 5px;
  background-color: #30475e;
  margin-top: 10px;
  position: absolute;
}

.profileUser-container h1 {
  margin-top: 80px;
  margin-bottom: 24px;
}

.profileUser-container ul {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 20px;
  list-style: none;
}

.profileUser-container ul li {
  background: #f0f2f5;
  padding: 24px;
  border-radius: 8px;
  position: relative;
}

.profileUser-container ul strong {
  display: block;
  margin-bottom: 16px;
  color: #41414d;
}

.profileUser-container ul p + strong {
  margin-top: 32px;
}

.profileUser-container ul li p {
  color: #737380;
  line-height: 21px;
  font-size: 16px;
}

